<template>
  <div style="height:300px;border:1px solid #ccc;overflow:auto;">
    <div v-for="item in items">{{item}}</div>
    <xa-infinite-scroll @load="onLoadMore" :loading="isLoading" :end="isEnd" :end-text="endText" />
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: 0,
      isLoading: false,
      isEnd: false,
      endText: '',
      p: 1,
      psize: 20
    }
  },
  methods: {
    onLoadMore () {
      this.isLoading = true
      console.log(`${this.p}`)
      setTimeout(() => {
        const data = Math.random() > 0.4 ? this.psize : 12  // , 40%
        this.isLoading = false
        this.items += data
        this.endText = this.items === 0 ? '' : `${this.items}`
        this.isEnd = data < this.psize
        this.p++
      }, 2000)
    }
  },
  created () {
    console.log(', 40%')
    this.onLoadMore()
  }
}
</script>




