<template>
  <div>
    <xa-popup v-model="isShowPopup" :from="from">
      
      <div style="height:300px;color:white;">
        <button @click="from='top'"></button>
        <button @click="from='bottom'"></button>
        <button @click="from='center'"></button>
        <button @click="from='left'"></button>
        <button @click="from='right'"></button>
      </div>
      
    </xa-popup>
    <button @click="isShowPopup=true"></button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isShowPopup: false,
      from: 'center'
    }
  }
}
</script>



